@import url('../components/personal-plan.css');


.header{
  background: #0B0B0B;
}

.title-main{
  margin-bottom: 30px;
  margin-top: 70px;
  @media (max-width: 767.98px) {
    margin-top: 35px;
  }
  .subtitle {
    p {
      margin-bottom: 3px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
  }
  h1{
    color: #0B0B0B;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    @media (max-width: 767.98px) {
      font-size: 30px;
      font-weight: 600;
      line-height: normal;
    }
  }
}


.about {
  background: #fff;
  padding-top: 110px;
  @media (max-width: 767.98px) {
    padding-top: 74px;
  }
  &__list{
    display: flex;
    padding-bottom: 120px;
    @media (max-width: 1023.98px) {
      padding-bottom: 80px;
    }
    @media (max-width: 767.98px) {
      display: block;
      padding-bottom: 56px;
    }
  }
  &__col{
    width: 50%;
    margin: 0 auto;
    &:first-child{
      border-right: 1px dotted #7B7B7B;
      @media (max-width: 767.98px) {
        border: 0;
      }
    }
    @media (max-width: 767.98px) {
      width: 100%;
      margin-bottom: 56px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  &__item{
    width: 436px;
    margin: 0 0 0 93px;
    @media (max-width: 1199.98px) {
      margin: 0 auto;
    }
    @media (max-width: 1023.98px) {
      width: 90%;
    }
    @media (max-width: 767.98px) {
      width: 100%;
    }
  }
  &__photo{
    width: 364px;
    height: 436px;
    margin-bottom: 45px;
    @media (max-width: 1199.98px) {
      margin: 0 auto 45px;
    }
    @media (max-width: 1023.98px) {
      width: 90%;
      margin: 0 auto 25px;
    }
    @media (max-width: 767.98px) {
      width: 100%;
      margin: 0 auto 32px;
    }
    img{
      border-radius: 6px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info{
    @media (max-width: 1199.98px) {
      width: 364px;
      margin: 0 auto;
    }
    @media (max-width: 1023.98px) {
      width: 90%;
    }
    @media (max-width: 767.98px) {
      width: 100%;
    }
  }
  &__name{
    color: #0B0B0B;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 6px;
    @media (max-width: 1023.98px) {
      font-size: 24px;
    }
  }
  &__position{
    color: #7339F5;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 19px;
  }
  &__description{
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    margin-right: 75px;
    @media (max-width: 1199.98px) {
      margin-right: 0;
    }
  }
}
.main-title .subtitle p {
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 5px;
}
.main-title .title .name {
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.8px;
}
.about-us{
  padding-top: 108px;
  background: #F6F6F8;
  @media (max-width: 1023.98px) {
    padding-top: 80px;
  }
  @media (max-width: 1023.98px) {
    padding-top: 56px;
  }
}
.about-us{
  &__description{
    margin-top: 28px;
  }
  &__item{
    display: flex;
    padding-bottom: 107px;
    @media (max-width: 1023.98px) {
      padding-bottom: 80px;
    }
    @media (max-width: 767.98px) {
      flex-wrap: wrap;
      padding-bottom: 56px;
    }
    &:nth-child(2n){
      .about-us__photo-block{
        order: 2;
        margin-right: 0;
        margin-left: 31px;
        @media (max-width: 767.98px) {
          margin-left: 0;
          order: 1;
        }
      }
      .about-us__text{
        order: 1;
        padding-top: 0;
        @media (max-width: 767.98px) {
          order: 2;
          margin-top: 32px;
        }
      }
    }
  }
  &__photo-block{
    min-width: 457px;
    width: 457px;
    height: 286px;
    margin-right: 31px;
    @media (max-width: 1023.98px) {
      min-width: 357px;
      width: 357px;
      height: auto;
    }
    @media (max-width: 767.98px) {
      min-width: 100%;
      width: 100%;
      margin-right: 0;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 6px;
    }
  }
  &__text{
    width: auto;
    flex: 1 1 auto;
    padding-top: 14px;
    @media (max-width: 767.98px) {
      padding-top: 32px;
    }
    h3, h2, h4, h5, h6{
      margin-top: 0;
      margin-bottom: 32px;
      @media (max-width: 767.98px) {
        margin-bottom: 24px;
      }
    }
    h2{
      color: #0B0B0B;
      font-size: 32px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media (max-width: 767.98px) {
        font-size: 22px;
      }
    }
    h3{
      color: #0B0B0B;
      font-size: 26px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media (max-width: 767.98px) {
        font-size: 20px;
      }
    }
    h4{
      color: #0B0B0B;
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media (max-width: 767.98px) {
        font-size: 18px;
      }
    }
    h5{
      color: #0B0B0B;
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media (max-width: 767.98px) {
        font-size: 16px;
      }
    }
    h6{
      color: #0B0B0B;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media (max-width: 767.98px) {
        font-size: 14px;
      }
    }
    p{
      color: #000;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 26px;
      margin-bottom: 26px;
      @media (max-width: 1023.98px) {
        &:last-child {
          margin-bottom: 0;
        }
      }
      @media (max-width: 1023.98px) {
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 20px;
      }
    }
  }
}